










import Vue from 'vue';
import ToolbarItem from '@/types/toolbar-item';

export default Vue.extend({
	props: {
		item: {
			required: true,
			type: Object as () => ToolbarItem,
		},
		value: {
			required: true,
			type: Object,
		},
	},
	methods: {
		runAction(): void {
			const data = {
				raw: this?.item.action(this?.value.raw),
			};
			if (Object.keys(this.value).length) {
				this.$emit('input', data);
			}
		},
	},
});
